.booking-order-container {
    display: flex;
    width: 100%;
    height: 100vh;
    font-family: 'Arial', sans-serif;
}

.form-container {
    flex: 1;
    padding: 20px;
    background-color: #f7f9fc;
    overflow-y: auto;
}

.map-container {
    flex: 1;
    height: 100%;
    background-color: #e0e7ff;
}

.step h2 {
    font-size: 20px;
    margin-bottom: 15px;
    color: #333;
}

input[type="text"],
select {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 14px;
}

.option-buttons {
    display: flex;
    justify-content: space-between;
    margin: 15px 0;
}

.option-button {
    flex: 1;
    text-align: center;
    padding: 10px;
    margin: 0 5px;
    border: 2px solid transparent;
    border-radius: 8px;
    background-color: #f0f4ff;
    cursor: pointer;
    transition: all 0.3s ease;
    font-weight: bold;
    font-size: 14px;
}

.option-button:hover {
    background-color: #d4e3ff;
}

.option-button.selected {
    border-color: #007bff;
    background-color: #007bff;
    color: white;
}

.shipping-info p,
.cost-info p {
    font-size: 16px;
    margin: 10px 0;
    color: #555;
}

ul {
    list-style: none;
    padding: 0;
}

ul li {
    padding: 8px;
    margin: 5px 0;
    background-color: #f3f3f3;
    border-radius: 4px;
    font-size: 14px;
}

.button-group {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}

button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
    font-size: 14px;
    cursor: pointer;
    transition: background-color 0.3s;
}

button:hover {
    background-color: #0056b3;
}

.cost-info p {
    font-weight: bold;
}

button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

@media (max-width: 768px) {
    .booking-order-container {
        flex-direction: column;
    }

    .form-container,
    .map-container {
        height: 50%;
        width: 100%;
    }

    .option-buttons {
        flex-direction: column;
    }

    .option-button {
        margin: 5px 0;
    }

    .button-group button {
        flex: 1;
        margin: 5px;
    }
}
